import React, { useState, useEffect, useRef } from "react";
import Layout from "../../components/Layout/Layout";
import DataTable, { createTheme } from "react-data-table-component";
import axios from "axios";
import { toast } from "react-toastify";
import { useReactToPrint } from "react-to-print";

const Patrimony = () => {
  const componentRef = useRef();

  // States
  const [selectedRows, setSelectedRows] = useState([]);
  const [records, setRecords] = useState([]); //table data
  const [filterRecords, setFilterRecords] = useState([]); //table data

  // Funo para carregar todos os patrimonios
  const getAllPatrimony = async () => {
    try {
      const { data } = await axios.get(`/api/patrimony/get-patrimony`);
      if (data.success) {
        setRecords(data?.patrimony);
        setFilterRecords(data?.patrimony);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllPatrimony();
  }, []);

  // Funo para formatar o preo
  const formatarPreco = (preco) => {
    return preco.toLocaleString("pt-BR", {
      style: "currency",
      currency: "BRL",
    });
  };

  // Funo para definir a tabela
  const columns = [
    {
      name: "Categoria",
      selector: (row) => row.category.name,
      sortable: true,
      width: "200px",
    },
    {
      name: "Descrio",
      selector: (row) => row.description,
      sortable: true,
      width: "450px",
    },
    {
      name: "Data-NF",
      selector: (row) => row.dtNota,
      sortable: true,
      width: "150px",
    },
    {
      name: "NF",
      selector: (row) => row.nota,
      sortable: true,
      width: "150px",
    },
    {
      name: "Preo",
      selector: (row) => row.vTotal,
      cell: (row) => formatarPreco(row.vTotal),
      sortable: true,
      width: "150px",
    },
    {
      name: "Aes",
      cell: (row) => (
        <a href={`/dashboard/admin/patrimony/${row._id}`}>
          <button className="btn btn-sm btn-success">
            <i className="fas fa-edit" />
          </button>
        </a>
      ),
      button: true,
      width: "80px",
    },
  ];

  // Funo tema da tabela
  createTheme(
    "solarized",
    {
      text: {
        primary: "##444C54",
        secondary: "##444C54",
      },
      background: {
        default: "##343A40",
      },
      context: {
        background: "#cb4b16",
        text: "##FFFFFF",
      },
      divider: {
        primary: "##073642",
      },
      action: {
        button: "rgba(0,0,0,.54)",
        hover: "rgba(0,0,0,.08)",
        disabled: "rgba(0,0,0,.12)",
      },
    },
    "light"
  );

  // Funo para customizar a tabela
  const customStyles = {
    rows: {
      style: {
        // minHeight: "72px", // override the row height
      },
    },
    headCells: {
      style: {
        textTransform: "uppercase",
        fontSize: "18px",
        fontWeight: "bold",
      },
    },
    cells: {
      style: {
        textTransform: "uppercase",
        fontSize: "15px",
      },
    },
  };

  const handleFilter = (e) => {
    const searchTerm = e.target.value.toLowerCase();

    const newData = filterRecords.filter(
      (row) =>
        row.category.name.toLowerCase().includes(searchTerm) ||
        row.description.toLowerCase().includes(searchTerm)
    );

    setRecords(newData);
  };

  // Funo para selecionar as linhas
  const handleRowSelected = (state) => {
    setSelectedRows(state.selectedRows);
  };

  // Funo para calcular o total
  const calculateTotal = () => {
    const total = selectedRows.reduce((acc, row) => acc + row.vTotal, 0);
    return total;
  };

  // Funo para imprimir
  const handlePrint = useReactToPrint({
    content: () => componentRef.current,
    documentTitle: "relao-de-patrimonio",
    onAfterPrint: () => {
      toast.success("Impresso realizada com sucesso.");
    },
  });

  return (
    <Layout>
      <section className="content-wrapper p-3">
        <div className="row">
          <div className="col-12">
            <div
              className="card"
              ref={componentRef}
              style={{ width: "100%", height: "100%" }}
            >
              <div className="card-header">
                <h3 className="card-title text-uppercase">
                  relao de patrimonio
                </h3>
                <div className="card-tools m-2">
                  <div
                    className="input-group input-group-md"
                    style={{ width: 350 }}
                  >
                    <button
                      className="btn btn-sm btn-primary mr-2"
                      onClick={handlePrint}
                    >
                      <i className="fas fa-print" />
                    </button>
                    <input
                      type="text"
                      name="table_search"
                      className="form-control float-right"
                      placeholder="Search"
                      onChange={handleFilter}
                    />
                    <div className="input-group-append">
                      <button type="submit" className="btn btn-default">
                        <i className="fas fa-search" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div className="card-body table-responsive p-2 mb-5">
                <DataTable
                  columns={columns}
                  data={records}
                  pagination={true}
                  theme="solarized"
                  responsive={true}
                  selectableRows={true}
                  fixedHeader={true}
                  paginationPerPage={5}
                  paginationRowsPerPageOptions={[5, 10, 15, 20, 25, 30]}
                  highlightOnHover={true}
                  customStyles={customStyles}
                  onSelectedRowsChange={handleRowSelected}
                  noDataComponent={<span>Nenhum registro encontrado.</span>}
                  paginationComponentOptions={{
                    rowsPerPageText: "Linhas por pgina:",
                    rangeSeparatorText: "de",
                    noRowsPerPage: false,
                    selectAllRowsItem: false,
                    selectAllRowsItemText: "Todos",
                  }}
                />
                <div>
                  <h5 className="col-13 m-3">
                    Valor total: {formatarPreco(calculateTotal())}
                  </h5>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Patrimony;
