import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";

const CreatePatrimony = () => {
  const navigate = useNavigate();

  const [categories, setCategories] = useState([]);
  const [category, setCategory] = useState("");
  const [description, setDescription] = useState("");
  const [dtNota, setDtNota] = useState("");
  const [nota, setNota] = useState("");
  const [location, setLocation] = useState('No informado');
  const [identifier, setIdentifier] = useState("");
  const [financiado, setFinanciado] = useState("");
  const [vTotal, setVTotal] = useState("");
  const [vPago, setVPago] = useState(0);
  const [vAPagar, setVAPagar] = useState(0);

  // get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`/api/category/get-category`);
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao carregar categorias.");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  const handleCreatePatrimony = async (e) => {
    e.preventDefault();
    try {
      const patrimonyData = new FormData();
      patrimonyData.append("category", category);
      patrimonyData.append("description", description);
      patrimonyData.append("dtNota", dtNota);
      patrimonyData.append("nota", nota);
      patrimonyData.append("identifier", identifier);
      patrimonyData.append("location", location);
      patrimonyData.append("financiado", financiado);
      patrimonyData.append("vTotal", vTotal);
      patrimonyData.append("vPago", vPago);
      patrimonyData.append("vAPagar", vAPagar);

      const { data } = await axios.post(
        `/api/patrimony/create-patrimony`,
        patrimonyData
      );

      if (data?.success) {
        toast.success("Patrimonio criado com sucesso.");
        navigate("/dashboard/admin/patrimony");
      } else {
        toast.error("Erro ao criar patrimonio.");
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao criar patrimonio.");
    }
  };

  return (
    <Layout>
      <div className="content-wrapper p-5">
        <div className="container-fluid">
          <div className="card col-12 m-2">
            <div className="card-body card-body">
              <p className="ext-uppercase">Cadastro</p>
              <form>
                <div className="row">
                  <div className="col-6">
                    <div className="form-group mb-2">
                      <select
                        className="form-control text-uppercase"
                        name="category"
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                      >
                        <option>Selecione uma categoria</option>
                        {categories.map((category) => (
                          <option key={category._id} value={category._id}>
                            {category.name}
                          </option>
                        ))}
                      </select>
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="input-group mb-2">
                      <select
                        type="text"
                        name="identifier"
                        value={identifier}
                        onChange={(e) => setIdentifier(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Identificador"
                        required
                      >
                        <option>Selecione um nome</option>
                        <option>Maria</option>
                        <option>Joao</option>
                      </select>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-6">
                    <div className="input-group mb-2">
                      <input
                        type="date"
                        name="dtNota"
                        value={dtNota}
                        onChange={(e) => setDtNota(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Data da nota fiscal"
                        required
                      />
                    </div>
                  </div>
                  <div className="col-6">
                    <div className="input-group mb-2">
                      <input
                        type="number"
                        name="nota"
                        value={nota}
                        onChange={(e) => setNota(e.target.value)}
                        className="form-control text-uppercase"
                        placeholder="Nota fiscal"
                        required
                      />
                    </div>
                  </div>
                </div>

                <div className="input-group mb-2">
                  <input
                    type="text"
                    name="description"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="descrio"
                    required
                  />
                </div>

                <div className="input-group mb-2">
                  <input
                    type="text"
                    name="location"
                    value={location}
                    onChange={(e) => setLocation(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Localizao"
                    required
                  />
                </div>
                <div className="input-group mb-2">
                  <select
                    type="text"
                    name="financiado"
                    value={financiado}
                    onChange={(e) => setFinanciado(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="O bem  financiado?"
                    required
                  >
                    <option>O bem  financiado?</option>
                    <option>sim</option>
                    <option>no</option>
                  </select>
                </div>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vPago"
                    value={vPago}
                    onChange={(e) => setVPago(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor pago"
                    required
                  />
                </div>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vAPagar"
                    value={vAPagar}
                    onChange={(e) => setVAPagar(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor a pagar"
                    required
                  />
                </div>
                <div className="input-group mb-2">
                  <input
                    type="number"
                    name="vTotal"
                    value={vTotal}
                    onChange={(e) => setVTotal(e.target.value)}
                    className="form-control text-uppercase"
                    placeholder="Valor total"
                    required
                  />
                </div>
                <div className="row">
                  <div className="col-12">
                    <button
                      type="submit"
                      className="btn btn-primary btn-block text-uppercase"
                      onClick={handleCreatePatrimony}
                    >
                      Salvar
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreatePatrimony;
