import { Routes, Route } from "react-router-dom";
import HomePage from "./pages/HomePage";
import Contact from "./pages/Contact";
import Pagenotfound from "./pages/Pagenotfound";
import Register from "./pages/Auth/Register";
import "react-toastify/dist/ReactToastify.css";
import Login from "./pages/Auth/Login";
import ForgotPassword from "./pages/Auth/ForgotPassword";
import AdminDashboard from "./pages/admin/AdminDashboard";
import UsuarioDashboard from "./pages/user/UsuarioDashboard";
import PrivateAdmin from "./components/Routes/PrivateAdmin";
import PrivateUsuario from "./components/Routes/PrivateUsuario";
import CreateCategory from "./pages/admin/CreateCategory";
import CreatePatrimony from "./pages/admin/CreatePatrimony";
import Category from "./pages/user/Category";
import Patrimony from "./pages/user/Patrimony";
import UpdatePatrimony from "./pages/admin/UpdatePatrimony";

function App() {
  return (
    <>
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/dashboard" element={<PrivateUsuario />}>
          <Route path="user" element={<UsuarioDashboard />} />
          <Route path="user/patrimony" element={<Patrimony />} />
          <Route path="user/patrimony/:_id" element={<UpdatePatrimony />} />
        </Route>
        <Route path="/dashboard" element={<PrivateAdmin />}>
          <Route path="admin" element={<AdminDashboard />} />
          <Route path="admin/category" element={<Category />} />
          <Route path="admin/register" element={<Register />} />
          <Route path="admin/create-category" element={<CreateCategory />} />
          <Route path="admin/create-patrimony" element={<CreatePatrimony />} />
          <Route path="admin/patrimony" element={<Patrimony />} />
          <Route path="admin/patrimony/:_id" element={<UpdatePatrimony />} />
        </Route>
        <Route path="/login" element={<Login />} />
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="*" element={<Pagenotfound />} />
      </Routes>
    </>
  );
}

export default App;
