import React, { useState, useEffect } from "react";
import Layout from "../../components/Layout/Layout";
import axios from "axios";
import { toast } from "react-toastify";
import { Modal } from "antd";
import EditCategory from "../../components/element/EditCategory";

const CategoryForm = () => {
  const [category, setCategory] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updateName, setUpdateName] = useState("");

  // get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(`/api/category/get-category`);
      if (data?.success) {
        setCategory(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Erro ao carregar categorias.");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/category/update-category/${selected._id}`,
        { name: updateName }
      );
      if (data.success) {
        toast.success(`Categoria ${updateName} atualizada com sucesso`);
        setSelected(null);
        setUpdateName("");
        setIsModalOpen(false);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  };

  // delete category
  const handleDelete = async (pId) => {
    try {
      const { data } = await axios.delete(
        `/api/category/delete-category/${pId}`);
      if (data.success) {
        toast.success(`Categoria ${category.name} apagada com sucesso`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  };

  return (
    <Layout>
      <section className="content-wrapper p-5">
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-header">
                <h3 className="card-title text-uppercase">
                  relao de categoria
                </h3>
                <div className="card-tools">
                  <div
                    className="input-group input-group-sm"
                    style={{ width: 250 }}
                  >
                    <input
                      type="text"
                      name="table_search"
                      className="form-control float-right"
                      placeholder="Search"
                    />
                    <div className="input-group-append">
                      <button type="submit" className="btn btn-default">
                        <i className="fas fa-search" />
                      </button>
                    </div>
                  </div>
                </div>
              </div>
              <div
                className="card-body table-responsive p-0"
                style={{ height: 400 }}
              >
                <table className="table table-head-fixed text-nowrap">
                  <thead>
                    <tr className="row">
                      <th className="col">Nome</th>
                      <th className="col-md-3">Ao</th>
                    </tr>
                  </thead>
                  <tbody>
                    {category.map((category) => (
                      <tr className="row" key={category._id}>
                        <td className="col text-uppercase">{category.name}</td>
                        <td className="col-md-3">
                          <button
                            className="btn btn-sm btn-success ml-2"
                            onClick={() => {
                              setIsModalOpen(true);
                              setUpdateName(category.name);
                              setSelected(category);
                            }}
                          >
                            <i className="fas fa-edit" />
                          </button>
                          <button
                            className="btn btn-sm btn-danger ml-2"
                            onClick={() => {
                              handleDelete(category._id);
                            }}
                          >
                            <i className="fas fa-trash" />
                          </button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
            <Modal
              onCancel={() => setIsModalOpen(false)}
              footer={null}
              open={isModalOpen}              
            >
              <EditCategory
                value={updateName}
                setValue={setUpdateName}
                handleSubmit={handleUpdate}
              />
            </Modal>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default CategoryForm;
