import { useState } from "react";
import Layout from "../../components/Layout/Layout";
import { toast } from "react-toastify";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const CreateCategory = () => {
  const [name, setName] = useState("");

  const navigate = useNavigate();

  const handleSubmint = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/category/create-category", {
        name,
      });
      if (data.success) {
        toast.success(`Categoria ${name} criada com sucesso`);
        setName(data.name);
        navigate("/dashboard/admin/category");
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error(error.response.data.message);
    }
  };

  return (
    <Layout>
      <div className="content-wrapper p-5">
        <div className="container-fluid">
          <div className="card col-12 m-2">
            <div className="card-body card-body">
              <p className="login-box-msg text-uppercase">
                Cadastrar categoria
              </p>
              <form onSubmit={handleSubmint}>
                <div className="input-group mb-3">
                  <input
                    type="text"
                    className="form-control text-uppercase"
                    placeholder="Nome da categoria"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                  />
                  <div className="input-group-append">
                    <div className="input-group-text">
                      <span className="fas fa-user" />
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-12">
                    <button
                      type="submit"
                      className="btn btn-primary btn-block text-uppercase"
                    >
                      Salvar
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CreateCategory;
