import React from "react";
import { useAuth } from "../../context/auth";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";

const MenuAdmin = () => {
  const navigate = useNavigate();

  const [auth, setAuth] = useAuth();

  function handleLogout() {
    setAuth({
      ...auth,
      user: null,
      token: "",
    });
    localStorage.removeItem("auth");
    toast.success("Logout realizado com sucesso");
    navigate("/");
  }

  return (
    <>
      <li className={`nav-item menu-open`}>
        <a href="#" className="nav-link active">
          <i className="nav-icon fas fa-duotone fa-user text-uppercase" />
          <p className="text-uppercase">
            Usurios
            <i className="right fas fa-angle-left" />
          </p>
        </a>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a href="/dashboard/admin" className="nav-link active">
              <i className="far fa-circle nav-icon" />
              <p className="text-uppercase">Listar</p>
            </a>
          </li>
        </ul>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a href="/dashboard/admin/register" className="nav-link active">
              <i className="far fa-circle nav-icon text-uppercase" />
              <p className="text-uppercase">Cadastrar</p>
            </a>
          </li>
        </ul>
      </li>
      <li className="nav-item menu-open">
        <a href="#" className="nav-link active">
          <i className="nav-icon fas fa-sharp fa-solid fa-layer-group text-uppercase" />
          <p className="text-uppercase">
            Categoria
            <i className="right fas fa-angle-left" />
          </p>
        </a>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a href="/dashboard/admin/category" className="nav-link active">
              <i className="far fa-circle nav-icon text-uppercase" />
              <p className="text-uppercase">Listar</p>
            </a>
          </li>
        </ul>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a
              href="/dashboard/admin/create-category"
              className="nav-link active"
            >
              <i className="far fa-circle nav-icon text-uppercase" />
              <p className="text-uppercase">Cadastrar</p>
            </a>
          </li>
        </ul>
      </li>
      <li className="nav-item menu-open">
        <a href="#" className="nav-link active">
          <i className="nav-icon fas fa-sharp fa-solid fa-layer-group" />
          <p className="text-uppercase">
            Patrimonio
            <i className="right fas fa-angle-left" />
          </p>
        </a>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a href="/dashboard/admin/patrimony" className="nav-link active">
              <i className="far fa-circle nav-icon text-uppercase" />
              <p className="text-uppercase">Listar</p>
            </a>
          </li>
        </ul>
        <ul className="nav nav-treeview">
          <li className="nav-item">
            <a
              href="/dashboard/admin/create-patrimony"
              className="nav-link active"
            >
              <i className="far fa-circle nav-icon" />
              <p className="text-uppercase">Cadastrar</p>
            </a>
          </li>
        </ul>
      </li>
      <li className="nav-item">
        <button
          type="button"
          className="btn btn-block btn-danger btn-sm mt-3 text-uppercase"
          onClick={!auth.user ? null : handleLogout}
        >
          Sair
        </button>
      </li>
    </>
  );
};

export default MenuAdmin;
